import React from "react";
import { createHashRouter, RouterProvider } from "react-router-dom";
import AdventCalendar from "./pages/AdventCalendar";
import About from "./pages/About";
import Outline from "./pages/Outline";
import ReactBlog from "./pages/ReactBlog";
import Home from "./pages/Home";
import ErrorPage from "./error.page";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A React component that renders the page {@link Header}
 * and the navigation {@link Menu} page. Page navigations are handled
 * by the {@link NavRouter}.
 * @component
 * @returns {(Header|Menu)} {@link Header} & {@link Menu} Container
 */

const router = createHashRouter([
  {
    path: "/",
    element: <Home />,
    errorElement: <ErrorPage />,
    children: [
      {
        element: <AdventCalendar />,
        index: true,
      },
      {
        path: "/about",
        element: <About />,
      },
      {
        path: "/outline",
        element: <Outline />,
      },
      {
        path: "/react",
        element: <ReactBlog />,
      },
    ],
  },
]);

function App() {
  return (
    <React.Fragment>
      <div>
        <RouterProvider router={router} />
      </div>
      <footer>
        <div className="py-2">
          &copy; 2024 Cynthia Altura Escano. All rights reserved. This website
          is licensed under the{" "}
          <a
            href="https://creativecommons.org/licenses/by-nc/4.0/legalcode"
            target="_blank"
            rel="noreferrer"
          >
            CC BY-NC 4.0
          </a>{" "}
          license.
        </div>
      </footer>
    </React.Fragment>
  );
}

export default App;
