import React, { useState } from "react";
import { Container, Row } from "react-bootstrap";
import Header from "../components/header/Header";
//import NavRouter from "../components/navigation/NavRouter";
import Menu from "../components/menu/Menu";
import { Outlet } from "react-router-dom";

export default function Home() {
  /**
   * Toggle for showing the Offcanvas menu
   */
  const [showMenu, setShowMenu] = useState(false);
  /**
   * Shows the menu items
   */
  const handleShowMenu = () => {
    setShowMenu(true);
  };
  /**
   * Hides the menu items
   */
  const handleHideMenu = () => {
    setShowMenu(false);
  };
  return (
    <Container fluid>
      <Header handleShowMenu={handleShowMenu} />
      <Menu showMenu={showMenu} handleHideMenu={handleHideMenu} />
      <Row className="my-3 justify-content-center">
        <div className="border border-info text-center flex align-items-center w-auto py-2">
          <span className="text-secondary">
            This is a demo app. See{" "}
            <a
              href="https://whm-portfolio.vercel.app/"
              className="text-decoration-none"
            >
              developer's page &rarr;
            </a>
          </span>
        </div>
      </Row>
      <Outlet />
    </Container>
  );
}
